"use client";

import { useState } from "react";
import SearchForm from "@/components/SearchForm";

export default function StockAnalyserPage() {
  const [searchQuery, setSearchQuery] = useState("");
  const [loading, setLoading] = useState(false);

  const handleSearch = (query: string) => {
    setLoading(true);
    setSearchQuery("");

    setTimeout(() => {
      setSearchQuery(query);
      setLoading(false);
    }, 1500);
  };

  return (
    <main className="min-h-screen w-full flex flex-col items-center justify-start bg-gradient-to-b from-[#e6f0ff] via-[#f0f5ff] to-white px-4 py-20 text-black">
      <h1 className="text-center text-5xl font-extrabold tracking-tight bg-gradient-to-r from-[#00C4A3] to-[#0057FF] bg-clip-text text-transparent mb-10">
        Stock Analyser
      </h1>

      <div className="w-full max-w-2xl mb-10">
        <SearchForm onSearch={handleSearch} />
      </div>

      {loading ? (
        <p className="text-xl font-semibold text-gray-500">
          Loading results...
        </p>
      ) : (
        <>
          {searchQuery && (
            <section className="w-full max-w-6xl grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6 mt-10">
              {[
                "AI Investment Insights",
                "Earnings Highlights",
                "Reddit Sentiment",
                "Financial Metrics",
                "Latest News",
              ].map((title, index) => (
                <div
                  key={index}
                  className="bg-white rounded-2xl shadow-md hover:shadow-lg hover:shadow-[#00C4A3]/30 transition-shadow p-6"
                >
                  <h2 className="text-xl font-semibold text-gray-900 mb-2">
                    {title}
                  </h2>
                  <p className="text-gray-500 text-sm">Coming soon...</p>
                </div>
              ))}
            </section>
          )}
        </>
      )}
    </main>
  );
}
